// setting page
var page = "";
if (document.getElementById("home")) {
  page = "home";
} else if (document.getElementById("contact")) {
  page = "contact";
}

// injecting all the SVGs into the code
var mySVGsToInject = document.querySelectorAll('img.inject-me');

SVGInjector(mySVGsToInject);

// adding smooth scrolls to all links
var smooth_scroll = new scroll('a[href*="#"]', {
  speed: 750,
});

// initing AOS
AOS.init({
  duration: 1500,
  offset: 200
});

// setting home page specfic things, like the testimonials slider and title animation
if (page == "home") {
  var lineDrawing = anime({
    targets: '.heading-primary__main-svg path',
    strokeDashoffset: [anime.setDashoffset, 0],
    easing: 'easeInOutSine',
    duration: 2000,
    delay: function (el, i) { return i * 10 },
    direction: 'alternate',
    loop: false
  });


  var tnsslider = tns({
    container: '.testimonials__slider',
    items: 1,
    slideBy: 'page',
    autoplay: true,
    controls: false,
    arrowKeys: true,
    autoplayHoverPause: true,
    autoplayButtonOutput: false,
    mouseDrag: true,
    nav: false
  });

  tnsslider.play();
}

